


.aximo-blog{
  &-section{
    padding: 0 0 100px;
    @include max(991px) {
      padding: 100px 0 70px;
    }
    @include max(767px) {
      padding: 80px 0 50px;
    }
    .aximo-section-title.center{
      max-width: 830px;
    }
  }
  &-wrap{
    margin-bottom: 30px;
    &:hover .aximo-blog-thumb img{
      transform: scale(1.1);
    }
    &:hover .aximo-blog-btn{
      color: #0000FF;
    }
    &:hover .aximo-blog-btn svg path{
      stroke: #0000FF;
    }
  }
  &-thumb{
    overflow: hidden;
    border-radius: 10px;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: all 0.4s;
    }
  }
  &-content{
    padding-top: 24px;
    h3{
      margin-bottom: 30px;
      font-family: $heading-font4;
      line-height: 1.33em;
      @include max(767px) {
        margin-bottom: 20px;
      }
    }
  }
  &-btn{
    font-weight: 700;
    display: flex;
    align-items: center;
    transition: all 0.4s;
    color: var(--heading-color3);
    svg {
      margin-left: 13px;
      path{
        transition: all 0.4s;
      }
    }
  }
  &-meta{
    margin-bottom: 24px;
    @include max(767px) {
      margin-bottom: 20px;
    }
    ul{
      li{
        padding: 0 12px;
        display: inline-block;
        position: relative;
        color: var(--heading-color3);
        letter-spacing: 0.5px;
        font-size: 16px;
        @include max(767px) {
          padding: 0 6px;
        }
        &:first-child{
          padding-left: 0;
        }
        &:last-child{
          padding-right: 0;
        }
        a{
          padding: 14px 23px;
          border-radius: 50px;
          display: inline-block;
          color: var(--heading-color3);
          border: 1px solid var(--heading-color3);
        }
      }
    }
  }
}