.aximo-project{
  &-thumb{
    overflow: hidden;
    border-radius: 20px;
    position: relative;
    border: 5px solid var(--white-smoke);
    img{
      transition: all 0.5s;
      transform: scale(1);
    }
    &:hover img{
      transform: scale(1.1);
    }
    &::before{
      content:"";
      left: 0;
      top: 0;
      z-index: 1;
      width: 100%;
      height: 100%;
      position: absolute;
      border-radius: 20px;
      transition: all 0.4s;
      background: linear-gradient(180deg, rgba(0, 0, 0, 0.00) 35.42%, #000 100%);
    }
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &-wrap{
    padding: 24px;
    bottom: 0;
    left: 0;
    z-index: 2;
    display: flex;
    align-items: center;
    position: absolute;
    @include max(767px) {
      display: block;
    }
  }
  &-data{
    flex: 0 0 85%;
    h3{
      margin-bottom: 10px;
      color: var(--white-smoke);
    }
    p{
      color: var(--white-smoke);
    }
  }
  &-icon{
    flex: 0 0 15%;
    text-align: right;
    @include max(767px) {
      margin-top: 20px;
      display: block;
      text-align: left;
    }
    svg path{
      transition: all 0.4s;
    }
    &:hover svg path{
      stroke: var(--accent-color);
    }
  }
}


.aximo-project-slider .swiper-pagination {
  max-width: 1296px;
  display: flex;
  position: static;
  margin: 0 auto;
  margin-top: 80px;
  justify-content: center;
  padding: 0 24px;
  @include max(991px) {
    margin-top: 60px;
  }
  @include max(767px) {
    margin-top: 40px;
  }
  

}
.aximo-project-slider .swiper-pagination-bullet {
  width: 25%;
  height: 3px;
  opacity: 1;
  border-radius: 0;
  margin: 0!important;
  transition: all .4s;
  background-color: #333;
}
.aximo-project-slider .swiper-pagination-bullet-active {
  background-color: var(--accent-bg);
 }

