
.pagination {
 justify-content: center;
}
.page-item {
  .page-link {
    height: 50px;
    width: 50px;
    background-color: transparent;
    color: #fff;
    border: none;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0px;
    border-radius: 0;
    font-size: 20px;
    font-weight: 700;
    color: rgba(19, 17, 26, 0.4);;
    text-align: center;
    // border: 1px solid var(--gray-700);
    &:focus {
      box-shadow: none;
      outline: none;
    }
    &:hover {
      // color: var(--gray-800);
      // background-color: var(--warning-500);
    }
    &.active{
      // color: var(--gray-800);
      // background-color: var(--warning-500);
    }
  }
  &:first-child {
    .page-link {
      border-radius: 5px 0px 0px 5px;
    }
  }
  &:last-child {
    .page-link {
      border-radius: 0px 5px 5px 0px;
    }
  }
  &:first-child,
  &:last-child {
    .page-link {
      &:hover{
        color: #0080ff;
      }
      svg{
        position: absolute;
        right: -22px;
        path{
          // stroke: var(--gray-10);
          transition: all 0.4s;
        }
        
      }
      &:hover path{
        stroke: #0080ff;
      }
      &:hover{
        background-color: transparent!important;
      }
    }
  }
  &.active {
    .page-link {
      color: white;
      // background-color: var(--gray-900);
    }
  }
}

.fugu-pagination{
  text-align: center;
    margin-top: 56px;
    @include max(767px) {
        margin-top: 35px;
    }
}




.fugo--pagination{
  .page-item {
    margin-right: 13px;
    &:last-child {
      margin-right: 0px;
    }
    .page-link {
      height: 55px;
      width: 60px;
      background-color: transparent;
      color: #fff;
      border: none;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 0px;
      border-radius: 10px;
      font-size: 20px;
      font-weight: 700;
      // color: var(--gray-10);
      text-align: center;
      @include max(479px) {
        width: 50px;
        height: 50px;
      }
      &:focus {
        box-shadow: none;
        outline: none;
      }
      &:hover {
        // background-color: var(--gray-900);
      }
    }
    &:first-child {
      .page-link {
        border-radius: 10px;
        svg{
          left: -22px;
          transform: rotate(180deg);
        }
      }
    }
    &:first-child,
    &:last-child {
      .page-link {
        &:hover{
          color: #0080ff;
        }
        svg{
          position: absolute;
          right: -22px;
          path{
            // stroke: var(--gray-10);
            transition: all 0.4s;
          }
          
        }
        &:hover path{
          stroke: #0080ff;
        }
        &:hover{
          background-color: transparent!important;
        }
      }
    }
    &.active {
      .page-link {
        color: white;
        // background-color: var(--gray-900);
      }
    }
  }
}
